@import '../../utils/vars';
@import '../../utils/fonts';

* {
  margin: 0;
}

@media (min-width: 360px) {
  .about {
    height: 500px;
    width: 100%;
    background-size: 335%;
    background-position-y: bottom;

    &__title {
      text-align: center;
      margin-top: -28px;
      padding: 0 16px;
    }

    &__paragraph {
      padding-left: 8px;
      line-height: 26px;
    }
  }
  .container--main {
    width: 328px;
  }
  
}

@media (min-width: 768px) {
  .container--main {
    width: 380px;
  }
  .about {
    height: 500px;
    background-size: 158.5%;
    background-position-y: bottom;

    &__title {
      margin-top: 0;
      padding: 0 10px;
    }
  }
}

@media (min-width: 1024px) {
  .about {
    background-size: 150%;
    height: 650px;
    background-position-y: bottom;
  }
}

@media (min-width: 1170px) {
  .about {
    margin: auto;
    width: 1170px;
    background-size: 133%;
    background-position-y: bottom;
  }
}

main {
  background-color: $backgroundColor;
}

.container--main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.about {
  display: flex;
  font-size: 16px;
  line-height: 26px;
  font-weight: 400;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  background-image: url('../../images/Background.jpeg');
  background-repeat: no-repeat;
  

  &__title {
    font-family: "Nunito", sans-serif;
    font-weight: 400;
    font-size: 40px;
    line-height: 40px;
    color: $baseColor;
  }

  &__paragraph {
    font-family: "Nunito", sans-serif;
    word-wrap: break-word;
    margin-top: 21px;
    font-size: 16px;
    line-height: 26px;
    color: $baseColor;
  }

  &__button {
    margin-top: 32px;
  }
}