@import '../utils/fonts';

.input {
  display: block;
  font-family: "Nunito", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  width: calc(100% - 18px);
  height: 52px;
  padding-left: 16px;
  color: #7e7e7e;
  background-color: #f8f8f8;
  border: 1px solid #D0CFCF;
  border-radius: 4px;
  outline: none;

  &:focus {
    color: black;

    &:invalid {
      border-color:#CB3D40;
    }
  }
}