@import '../../utils/fonts';

* {
  margin: 0;
  padding: 0;
}

$padding: 20px;

.user {
  height: calc(254px - 2*$padding);
  padding: $padding;
  font-family: 'Nunito', sans-serif;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  background-color: #ffffff;

  &__photo {
    margin: 0 auto;
    height: 70px;
    width: 70px;
    border-radius: 50%;
  }

  &__position {
    margin-top: 20px;
  }

  &__name, &__email{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
  }
  
  &__info {
    text-align: center;
    font-size: 16px;
    line-height: 26px;
    margin-top: 20px;
    height: 78px;
  }
}

.tooltip {
  display: none;
}