@import '../../utils/vars';
* {
  margin: 0;
  padding: 0;
}

@media (min-width: 360px) {
  .new-user {
    &__form {
      width: 328px;
    }
  }
}

@media (min-width: 768px) {
  .new-user {
    &__form {
      width: 380px;
    }
  }
}

.new-user {
  margin: auto;
  padding-top: 141px;
  padding-bottom: 100px;

  &__title {
    text-align: center;
    font-family: "Nunito", sans-serif;
    font-size: 40px;
    line-height: 40px;
    font-weight: 400;
    margin-bottom: 50px;
  }

  &__form {
    margin: auto;
  }
}

.field-file {
    display: flex;
    font-family: "Nunito", sans-serif;
    font-weight: 400;

  &__label {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 54px;
    width: 83px;
    font-size: 16px;
    background-color: #f8f8f8;
    border: 1px solid rgba(0, 0, 0, 0.87);
    border-radius: 4px 0 0 4px ;

    &:focus {
      .field-file__label--text {
        
      }
    }

    &--text {
      display: inline-block;
      flex-grow: 1;
      font-size: 16px;
      line-height: 52px;
      padding-left: 16px;
      right: 83px;
      color: #7e7e7e;
      background-color: #f8f8f8;
      border: 1px solid #D0CFCF;
      border-radius: 0 4px 4px 0;
    }
  }

  &__input {
    display: none;
  }

}

.form {
  font-family: "Nunito", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  &__position {
    &:first-of-type {
      margin-top: 8px;
    }
    margin-top: 7px;
  }

  &__img {
    display: block;
    margin: auto;
  }

  &__title {
    margin-top: 25px;
  }

  &__text, &__email {
    margin-bottom: 50px;
  }

  &__phone {
    margin-bottom: 4px;
  }

  &__phone-description {
    color: #7E7E7E;
    font-size: 12px;
    line-height: 14px;
    padding-left: 16px;
  }

  &__file {
    margin-top: 47px;
  }

  &__button {
    display: block;
    margin: 50px auto 0 auto;
  }
}

input[type="radio"] {
  position: absolute;
  opacity: 0;
  + .radio__label {
    &::before {
      content: '';
      background-color: $baseColor;
      border-radius: 100%;
      border: 1px solid $disabledColor;
      display: inline-block;
      width: 20px;
      height: 20px;
      position: relative;
      margin-right: 12px;
      vertical-align:middle;
      cursor: pointer;
      text-align: center;
    }
  }
  &:focus {
    + .radio__label {
      &::before {
        background-color: $secondaryColor;
        box-shadow: inset 0 0 0 4px $baseColor;
        border-color: $secondaryColor;
      }
    }
  }
}

.uploaded {
  color:rgba(0, 0, 0, 0.87)
}