@import '../../utils/vars';
* {
  margin: 0;
  padding: 0;
}

@media (min-width: 360px) {
  .users {
    width: 328px;
    &__list {
      gap: 20px;
    }
  }
}

@media (min-width: 768px) {
  .users {
    width: 704px;
    &__list {
      gap: 16px
    }
  }
}

@media (min-width: 1024px) {
  .users {
    width: 904px;
    &__list {
      gap: 29px;
    }
  }
}

@media (min-width: 1170px) {
  .users {
    width: 1170px;
    &__list {
      gap: 29px;
    }
  }
}

.users {
  margin: auto;
  padding-top: 140px;
  display: flex;
  flex-direction: column;

  &__title {
    font-family: "Nunito", sans-serif;
    text-align: center;
    font-size: 40px;
    line-height: 40px;
    font-weight: 400;
    margin-bottom: 50px;
  }
  
  &__list {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(auto-fit, minmax(282px, 1fr));
  }

  &__button {
    margin: 50px auto 0 auto;

    &--disabled {
      display: none;
    }
  }
}