@import '../../utils/vars';

@media (min-width: 360px) {
  .logo {
    margin-left: 16px;
  }
  .container {
    margin-right: 16px;
  }
}

@media (min-width:768px) {
  .logo {
    margin-left: 32px;
  }
  .container {
    margin-right: 32px;
  }
}

@media (min-width:1024px) {
  .logo {
    margin-left: 60px;
  }
  .container {
    margin-right: 60px;
  }
}

@media (min-width: 1170px) {
  .logo {
    margin-left: calc((100% - 1170px)/2);
  }
  .container {
    margin-right: calc((100% - 1170px)/2);
  }
}

.header {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $baseColor;
}

.logo {
  width: 104px;
  height: 26px;
};

.nav-button {
  &:first-child {
    margin-right: 10px;
  }
}


