@import '../utils/vars';

.button {
  font-family: "Nunito", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 26px;
  border: 0px;
  border-radius: 80px;
  background-color: $primaryColor;
  height: 34px;
  width: 100px;

  &--disabled {
    display: none;
  }

  &--freezed {
    background-color: #b4b4b4;
    color: #ffffff;
  }

  &:hover {
    background-color: $hoverColor;
  }
}